import React, { useState, useEffect, useRef } from "react";
import $ from "jquery";
import '../../styles/style.css';

let autoComplete;

const loadScript = (url, callback) => {
	let script = document.createElement("script");
	script.type = "text/javascript";

	if (script.readyState) {
		script.onreadystatechange = function() {
			if (script.readyState === "loaded" || script.readyState === "complete") {
				script.onreadystatechange = null;
				callback();
			}
		};
	} else {
		script.onload = () => callback();
	}
	script.src = url;
	document.getElementsByTagName("head")[0].appendChild(script);
};

function handleScriptLoad(updateQuery, autoCompleteRef, changeLocation) {
	autoComplete = new window.google.maps.places.Autocomplete(
		autoCompleteRef.current,
		{ types: ["(cities)"], componentRestrictions: {} }
	);
	autoComplete.addListener("place_changed", () =>
			handlePlaceSelect( updateQuery, changeLocation )
	);

	setTimeout(() => {
		$(".pac-container").prependTo( $("#search-location-input") );
	}, 800)
}

async function handlePlaceSelect(updateQuery, changeLocation) {
	const addressObject = autoComplete.getPlace();
	const query = addressObject.formatted_address;

	const latitude = addressObject.geometry.location.lat().toFixed(2);
	const longitude = addressObject.geometry.location.lng().toFixed(2);

	updateQuery(query.toUpperCase());
	changeLocation(latitude, longitude);
}

const SearchLocationInput = ({changeLocation}) => {
	const [query, setQuery] = useState("");
	const autoCompleteRef = useRef(null);

	useEffect(() => {
		loadScript(
			`https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}&libraries=places`,
			() => handleScriptLoad(setQuery, autoCompleteRef, changeLocation) 
		);
	}, []);

	return (
		<div id="search-location-input" style={{position: 'relative'}}>
			<input
				className="input-area"
				type="text"
				ref={autoCompleteRef}
				onChange={event => setQuery( event.target.value )}
				value={query}
				id="city-location-select"
				placeholder="selecciona tu ubicación"
			/>
		</div>
	);
}

export default SearchLocationInput;