import {setError} from '../error/errorActions';
import {
	APP_INIT,
	fetchProducts
} from './cartActions';

export const cartMiddleware = () => next => async action => {
	switch (action.type) {
		case APP_INIT:
			try {
				const products = JSON.parse(localStorage.getItem('starCart')); // cart
				await next(fetchProducts(products));

			} catch (errors) {
				console.log(errors);
			}
			break;
		default:
			break;
	}
	return next(action);
};
