import React from "react";
import StepBar from "../base/ StepBar";
import "./CartPage.css"
import CartItem from "../cart/CartItem";
import CartTotals from "../cart/CartTotals";
import {connect} from "react-redux";
import Header from "../header/StarHeader";
import EmptyCart from "./EmptyCart";
import {Link as RouterLink} from "react-router-dom";
import BaseButton from "../base/BaseButton";
import appConstants from "../../configs/constants.config";

const CartPage = ( {items} ) => {
	return (
		<>
			<Header
				pageTitle={'Carrito de compra'}
			/>
			<div className="wrapper" id="custom_cart">
				<div className="page-content">

					<StepBar/>
						<div className={`cart-form-wrapper ${items.length === 0 ? "empty-cart-from-wrapper" : ""}`}>
							<div className="cart-form">
								{items.length !== 0 ?
									items.map( item => <CartItem key={item.id} product={item}/> )
									:
									(
										<div>
											<EmptyCart/>
											<BaseButton
												size="large"
												style={{display: 'flex', margin: '0 auto', marginTop: 30, width: '80%'}}
												component={RouterLink}
												to={'/editor'}>Ir a la tienda
											</BaseButton>
										</div>
									)
								}
							</div>
							{ items.length !== 0 && (<CartTotals
								currentStep={appConstants.PROGRESS_BAR_STEPS.CART}
							/>  )}
						</div>
				</div>
			</div>
		</>
	)
};

const mapStateToProps = ( state ) => {
	return {
		items: state.cart.items
	}
};

export default connect( mapStateToProps )( CartPage )
