import {
    ADD_TO_CART,
    DELETE_FROM_CART,
    FETCH_PRODUCTS,
    CHANGE_PRODUCT_PROP,
    CHANGE_ORDER_PROP,
    CREATE_ORDER, CLEAR_STATE
} from "./cartActions";
import {success} from "redux-saga-requests";

const initialState = {
    items: [],
    order: {
        shippingType: 2,
        total: 0,
        subTotal: 0
    },
    confirm: {}
};

export default (state = initialState, action) => {
    switch (action.type) {
        case ADD_TO_CART:
            const addToCartProducts = [action.payload.product, ...state.items];
            const addToCartTotalPrice = addToCartProducts.reduce((a, b) => a + (b.price || 0), 0);

            return {
                ...state,
                items: [action.payload.product, ...state.items],
                order: {...state.order,
                    total: state.order.shippingType === 2 ? addToCartTotalPrice : (addToCartTotalPrice + 30),
                    subTotal: addToCartTotalPrice
                }
            };
        case DELETE_FROM_CART:
            const deleteFromCartProducts = [...state.items.filter(item => item.id !== action.payload.id)];
            const deleteFromCartTotalPrice = deleteFromCartProducts.reduce((a, b) => a + (b.price || 0), 0);;

            return {
                ...state,
                items: [...state.items.filter(item => item.id !== action.payload.id)],
                order: {...state.order,
                    total: state.order.shippingType === 2 ? deleteFromCartTotalPrice : (deleteFromCartTotalPrice + 30),
                    subTotal: deleteFromCartTotalPrice
                }
            };
        case FETCH_PRODUCTS:
            const totalPrice = [...action.payload].reduce((a, b) => a + (b.price || 0), 0);

            return {
                ...state,
                items: [...action.payload],
                order: {...state.order,
                    total: state.order.shippingType === 2 ? totalPrice : (totalPrice + 30),
                    subTotal: totalPrice
                }
            };
        case CHANGE_PRODUCT_PROP:
            const updateItemId = state.items.findIndex((item) => item.id === action.payload.productId);
            const item = {...state.items[updateItemId], [action.payload.prop]: action.payload.value};
            const newProductsArray = [...state.items.slice(0, updateItemId), item, ...state.items.slice(updateItemId + 1)];
            const newTotalPrice = newProductsArray.reduce((a, b) => a + (b.price || 0), 0);

            return {
                ...state,
                items: newProductsArray,
                order: {...state.order,
                    total: state.order.shippingType === 2 ? newTotalPrice : (newTotalPrice + 30),
                    subTotal: newTotalPrice
                }
            };
        case CHANGE_ORDER_PROP:
            return {
                ...state,
                order: {
                    ...state.order,
                    [action.payload.prop]: action.payload.value,
                    total: action.payload.value === 2 ? (state.order.total - 30) : (state.order.total + 30)
                }
            };
        case success(CREATE_ORDER):
            return {
                ...state,
                confirm: action.payload.data,
            };
        case CLEAR_STATE:
            return {
                ...initialState
            }
        default:
            return state;
    }
};
