import React, {Fragment} from 'react';

import './CartTotals.css';
import BaseButton from '../base/BaseButton';
import FormControl from '@material-ui/core/FormControl';
import {Link as RouterLink} from "react-router-dom";
import TextField from "@material-ui/core/TextField";
import {MenuItem} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import appConstants from '../../configs/constants.config';
import {connect} from "react-redux";
import {changeOrderProp} from "../../store/cart/cartActions";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";

const CartTotals = ({ currentStep, order, items, changeOrderProp }) => {
	const onChangeShippingType = ( event ) => {
		changeOrderProp('shippingType', event.target.value);
	};

	const onProceedToCheckout = () => {
		// cart (products)
		localStorage.setItem('starCart', JSON.stringify(items))

		// cartTotal
		localStorage.setItem('starOrder', JSON.stringify(order))
	};


	const shippingLabel = (shippingKey) => {
		for (const value of shippingType) {
			if (value.key === shippingKey) {
				return value.label;
			}
		}
	}

	const shippingType = [
		{
			key: 2,
			label: 'Standard ( FREE )'
		},
		{
			key: 1,
			label: 'Express ( 30$ )'
		}
	];

	const useStyle = makeStyles( ( theme ) => (
		{
			root: {
				'& .MuiInputBase-root': {
					margin: theme.spacing( 1 ),
					marginTop: 15,
					width: '20ch',
					background: 'transparent'
				}
			}
		}
	) )

	const classes = useStyle();

	return (
		<Fragment>
			<div className="cart-totals-wrapper">

				<div className="cart-totals">
					<div className="cart-totals__items">
						<div className="item-name">Total parcial</div>
						<div className="item-value" style={{fontWeight: 900}}>{`$${order.subTotal}`}</div>
					</div>
					<div className="cart-totals__items" style={{flexDirection: "column"}}>

						<div className="shipping" style={{marginBottom: 0, fontSize: 20}}>Envío</div>

						<RadioGroup aria-label="gender" name="gender1" value={order.shippingType} hidden={currentStep === appConstants.PROGRESS_BAR_STEPS.CART}>
							<FormControlLabel value={order.shippingType} control={<Radio />} label={shippingLabel(order.shippingType)} />
						</RadioGroup>

						{/*<div className="shipping" hidden={currentStep === appConstants.PROGRESS_BAR_STEPS.CHECKOUT}>
							<FormControl component="fieldset">
								<TextField
									className={classes.root}
									id="filled-select-currency"
									style={{background: 'transparent'}}
									select
									label="Elija el tipo"
									value={order.shippingType}
									onChange={onChangeShippingType}
									variant="filled"
								>
									{shippingType.map( option => (
										<MenuItem key={option.key} value={option.key}>
											{option.label}
										</MenuItem>
									) )}
								</TextField>
							</FormControl>
						</div>*/}
						<p style={{fontSize: "14px", fontWeight: 200}}>Envió gratuito, recibe tu mapa estelar dentro de 2 a 5 días hábiles, si es digital, en menos de 24hrs.</p>

					</div>
					<div className="cart-totals__items" style={{fontSize: "20px", fontWeight: 600}}>
						<div className="item-name">Total</div>
						<div className="item-value">{`$${order.total}`}</div>
					</div>
				</div>

				<div className="proceed-to-checkout">
					 <BaseButton
						size="large"
						style={{
							margin: 0,
							marginBottom: 10,
							width: '100%'
						}}
						onClick={onProceedToCheckout}
						component={RouterLink}
						to={'/checkout'}
					>
						{'Continuar'}
					</BaseButton>

					{currentStep === appConstants.PROGRESS_BAR_STEPS.CART && items.length < 2 && <BaseButton
						size="large"
						style={{margin: 0, width: '100%'}}
						component={RouterLink}
						to={'/editor'}>Crea otra impresión
					</BaseButton>}
				</div>
			</div>
		</Fragment>
	);
}

const mapStateToProps = ( state ) => {
	return {
		items: state.cart.items,
		order: state.cart.order
	}
};

export default connect( mapStateToProps, {changeOrderProp} )( CartTotals )
