import React, {Component, Fragment} from 'react';
import CartScrollBar from './CartScrollBar';
import EmptyCart from '../pages/EmptyCart';
import CSSTransitionGroup from 'react-transition-group/TransitionGroup';

import '../../styles/scss/style.scss';
import {Link as RouterLink} from "react-router-dom";
import BaseButton from "../base/BaseButton";

class Header extends Component {
	constructor( props ) {
		super( props );
		this.state = {
			showCart: false,
			cart: this.props.cartItems || [],
			mobileSearch: false
		};
	}

	handleCart( e ) {
		e.preventDefault();
		this.setState( {
			showCart: !this.state.showCart
		} )
	}

	render() {
		let cartItems;

		cartItems = this.state.cart.map( product => {
			return (
				<li className="cart-item" style={{
					paddingLeft: 15,
					paddingRight: 25,
					paddingTop: 20
				}} key={product.id}>
					<img alt="bla" className="product-image" src={product.thumbnail}/>

					<div style={{marginLeft: 15}}>
						{product.date &&
						 <span>
								 <strong>Date: </strong>
							 {product.date}<br/>
							 </span>
						}
						{product.location &&
						 <span>
								 <strong>Location: </strong>
							 {product.location}<br/>
							 </span>
						}
						{product.quantity &&
						 <span>
								 <strong>Quantity: </strong>
							 {product.quantity}<br/>
							 </span>
						}
						{product.coordinate &&
						 <span>
								 <strong>Coordinate: </strong>
							 {product.coordinate}<br/>
							 </span>
						 }

						<div style={{marginTop: 0, fontWeight: 700, fontSize: 20}}>
							{`$${product.price}`}
						</div>
					</div>
				</li>
			)
		} );
		let view;

		if ( cartItems.length <= 0 ) {
			view = <EmptyCart/>
		} else {
			view = <CSSTransitionGroup
				transitionName="fadeIn"
				transitionEnterTimeout={500}
				transitionLeaveTimeout={300}
				component="ul"
				className="cart-items"
			>{cartItems}</CSSTransitionGroup>
		}
		return (
			<header style={{padding: '6px 32px', opacity: '0.97'}}>
				<div className="container">
					<div className="brand">
						<a href="/">
							<img className="logo" src="images/tucielodenoche-icon.png" alt="Veggy Brand Logo"
							     style={{width: 65}}/>
						</a>
					</div>

					<div className="cart" style={{
						alignItems: 'center',
						marginLeft: 'auto'
					}}>

						{this.props.isHome ?
							(<Fragment>
								<div className="cart-info">
									<table>
										<tbody>
										<tr>
											<td>No. de artículos</td>
											<td>:</td>
											<td><strong style={{fontWeight: 900}}>{this.props.totalItems}</strong></td>
										</tr>
										<tr>
											<td>Total parcial</td>
											<td>:</td>
											<td><strong style={{fontWeight: 900}}>{`$${this.props.total}`}</strong></td>
										</tr>
										</tbody>
									</table>
								</div>
								<a className="cart-icon" href="/cart" onClick={this.handleCart.bind( this )}
								   ref="cartButton">
									<img className={this.props.cartBounce ? "tada" : " "} src="images/shopping-cart.png"
									     alt="Cart"/>
									{this.props.totalItems ?
										<span className="cart-count">{this.props.totalItems}</span> : ""}
								</a>
								<div className={this.state.showCart ? "cart-preview active" : "cart-preview"}
								     ref="cartPreview">
									<CartScrollBar>
										{view}
									</CartScrollBar>
									<div className="action-block">

										{this.state.cart.length > 0 && <BaseButton
											size="large"
											style={{margin: 0, marginBottom: 10, width: '100%'}}
											component={RouterLink}
											to={'/cart'}>Ver carrito
										</BaseButton>}

										<BaseButton
											disabled={this.state.cart.length <= 0}
											size="large"
											style={{margin: 0, marginBottom: 5, width: '100%'}}
											component={RouterLink}
											to={'/checkout'}>Pasar por la caja
										</BaseButton>
									</div>
								</div>
							</Fragment>)
							:
							(
								<h2 style={{fontWeight: 'bold'}}>{this.props.pageTitle}</h2>
							)
						}

					</div>
				</div>
			</header>
		)
	}
}

export default Header;
