import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import StepBar from "../base/ StepBar";
import './ConfirmPage.css';
import Header from "../header/StarHeader";
import {Link as RouterLink} from "react-router-dom";
import BaseButton from "../base/BaseButton";
import uuid from "uuid";
import Product from "../cart/Product";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import {connect} from "react-redux";
import {Redirect} from "react-router-dom";
import _ from 'lodash';
import {clearState} from '../../store/cart/cartActions';

const useStyles = makeStyles((theme) => ({
    listItem: {
        padding: theme.spacing(1, 0),
    },
    total: {
        fontWeight: 700,
    },
    title: {
        marginTop: theme.spacing(2),
        fontWeight: 600
    },
    orderDetails: {
        padding: theme.spacing(2),
        backgroundColor: theme.palette.grey[200],
    },
    orderSummary: {
        padding: theme.spacing(5, 0),
        justifyContent: 'space-evenly',
        marginTop: 20
    },
    orderSummaryItem: {
        borderLeft: '1px dashed black',
        padding: theme.spacing(2)
    },
    table: {
        minWidth: 650
    },
    confirmCardValue: {
        fontSize: '1rem',
        fontFamily: 'Roboto, Helvetica, Arial, sans-serif',
        fontWeight: 400,
        lineHeight: 1.5,
        letterSpacing: '0.00938em'
    }
}));

const Confirm = ({ items, confirm, clearState }) => {
    const classes = useStyles();

    return (
        <>
            {_.isEmpty(confirm) && <Redirect to={'/'}/>}

            <Header
                pageTitle={'Confirmar'}
            />
            <div className="wrapper" id="custom_cart">
                <div className="page-content">
                    <StepBar activeStep="2"/>
                    <div style={{backgroundColor: 'transparent', padding: '0 20px'}}>
                        <Typography variant="h3" style={{fontWeight: 600, textAlign: 'center'}} gutterBottom>
                            Orden recibida
                        </Typography>
                        <Typography variant="h5"  style={{textAlign: 'center'}} gutterBottom>
                            Gracias. Tu pedido ha sido recibido.
                        </Typography>

                        <Grid container spacing={2} justify="flex-start" className={classes.orderSummary}>
                            <Card style={{width: '50%'}} className="confirm-card-panel">
                                <CardContent>
                                    <Grid item xs={12}>
                                        <Typography variant="h6" gutterBottom>
                                            Número de orden: <span className={classes.confirmCardValue}>{confirm.id}</span>
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Typography variant="h6" gutterBottom>
                                            Fecha: <span className={classes.confirmCardValue}>{confirm.created}</span>
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Typography variant="h6" gutterBottom>
                                            Email: <span className={classes.confirmCardValue}>{confirm.email}</span>
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Typography variant="h6" gutterBottom>
                                            Total: <span className={classes.confirmCardValue}>${confirm.total}</span>
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Typography variant="h6" gutterBottom>
                                            Método de pago: <span className={classes.confirmCardValue}>Tarjeta de crédito (Stripe)</span>
                                        </Typography>
                                    </Grid>
                                </CardContent>
                            </Card>
                        </Grid>

                        {items.map(product => (
                            <div className="cart-product" key={uuid}>
                                <Product product={product} key={uuid}/>
                            </div>
                        ))}
                    </div>
                    <BaseButton
                        onClick={clearState}
                        size="large"
                        style={{display: 'flex', margin: '0 auto', marginTop: 40, marginBottom: 20, width: '80%'}}
                        component={RouterLink}
                        to={'/'}>Ir a la página de inicio
                    </BaseButton>
                </div>
            </div>
        </>
    );
}

const mapStateToProps = ( state ) => {
    return {
        items: state.cart.items,
        confirm: state.cart.confirm
    }
};

export default connect( mapStateToProps, {clearState} )( Confirm );