import React, {useState} from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
import {CardElement, useElements, useStripe} from "@stripe/react-stripe-js";
import BaseButton from "../base/BaseButton";
import {createOrder} from "../../store/cart/cartActions";
import {Link as RouterLink} from "react-router-dom";
import {withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import axios from "axios";
import {Redirect} from "react-router-dom";

const CheckoutForm = ( {order, items, createOrder, setLoading, history} ) => {
	const [isProcessing, setProcessingTo] = useState( false );
	const [checkoutError, setCheckoutError] = useState();
	const [checkCompleteStripe, setCompleteStripe] = useState(false);

	const stripe = useStripe();
	const elements = useElements();

	const shippingLabel = ( shippingKey ) => {
		for ( const value of shippingType ) {
			if ( value.key === shippingKey ) {
				return value.label;
			}
		}
	}

	const shippingType = [
		{
			key: 2,
			label: 'Estándar (GRATIS) de 2 a 5 días habiles. Digital en 24hrs.'
		},
		{
			key: 1,
			label: 'Express ( 30$ )'
		}
	];

	const iframeStyles = {
		base: {
			color: "rgba(0, 0, 0, 0.80)",
			backgroundColor: "#f9f9f9",
			fontSize: "16px",
			iconColor: "#000",
			"::placeholder": {
				color: "rgba(0, 0, 0, 0.90)"
			}
		},
		invalid: {
			iconColor: "#e0123b",
			color: "#e0123b"
		},
		complete: {
			iconColor: "#0ccf02"
		}
	};

	const cardElementOpts = {
		iconStyle: "solid",
		style: iframeStyles,
		hidePostalCode: true
	};

	const onPlaceOrder = async ev => {
		ev.preventDefault();

		if ( !checkCompleteStripe ) {
			setCheckoutError( 'Please fill all fields.' );
			return;
		}

		// prepare data
		const billingDetails = {
			name: ev.target.first_name.value,
			email: ev.target.email.value
		};
		const shippingDetails = {
			country: ev.target.shipping_country.value,
			state: ev.target.shipping_state.value,
			city: ev.target.shipping_city.value,
			line1: ev.target.shipping_address.value,
			postal_code: ev.target.shipping_zip.value,
			phone: ev.target.shipping_phone.value,
			last_name: ev.target.last_name.value,
			order_comment: ev.target.order_comment.value
		};
		const orderData = {
			billingDetails,
			shippingDetails,
			order,
			products: items
		};

		setProcessingTo( true );
		setLoading(true);

		const cardElement = elements.getElement( "card" );

		try {
			const {data: clientSecret} = await axios.post( process.env.REACT_APP_BASE_URL_PROD + '/payment_intents', {
				amount: order.total * 100,
				currency: "mxn"
			});

			const paymentMethodReq = await stripe.createPaymentMethod( {
				type: "card",
				card: cardElement,
				billing_details: billingDetails
			} );

			if ( paymentMethodReq.error ) {
				setCheckoutError( paymentMethodReq.error.message );
				setProcessingTo( false );
				setLoading(false);
				return;
			}

			const {error} = await stripe.confirmCardPayment( clientSecret, {
				payment_method: paymentMethodReq.paymentMethod.id
			});

			if ( error ) {
				setCheckoutError( error.message );
				setProcessingTo( false );
				setLoading(false);
				return false;
			}

			await onSuccessfulCheckout( orderData );

		} catch ( err ) {
			setCheckoutError( err.message );
		}
	};

	const onSuccessfulCheckout = async ( orderData ) => {
		localStorage.removeItem('starCart');
		localStorage.removeItem('starOrder');

		await createOrder(orderData);

		setLoading(false);
		setProcessingTo( false );

		history.push('/confirm');
	}

	const handleCardDetailsChange = ev => {
		setCompleteStripe(ev.complete);
		ev.error ? setCheckoutError( ev.error.message ) : setCheckoutError();
	};

	return (
		<React.Fragment>
			
			{items.length === 0 && <Redirect to={'/'}/>}

			<form method="post" onSubmit={onPlaceOrder}>
				<div className="cart-form-wrapper">
					<div className="cart-form">
						<Typography variant="h6" gutterBottom>
							Detalles de facturación
						</Typography>
						<Grid container spacing={3}>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									name="first_name"
									label="Nombre de pila"
									fullWidth
									autoComplete="given-name"
								/>
							</Grid>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									name="last_name"
									label="Apellido"
									fullWidth
									autoComplete="family-name"
								/>
							</Grid>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									name="email"
									label="Dirección de correo electrónico"
									fullWidth
								/>
							</Grid>
						</Grid>

						<Typography variant="h6" style={{marginTop: 60}}>
							Detalles de envío
						</Typography>
						<Grid container spacing={3}>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									name="shipping_country"
									label="País"
									fullWidth
									autoComplete="shipping country"
								/>
							</Grid>
							<Grid item xs={12} sm={6}>
								<TextField
									name="shipping_state"
									label="Estado / Provincia / Región"
									fullWidth
								/>
							</Grid>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									name="shipping_city"
									label="Ciudad"
									fullWidth
									autoComplete="shipping address-level2"
								/>
							</Grid>
							<Grid item xs={12}>
								<TextField
									required
									name="shipping_address"
									label="Dirección"
									fullWidth
									autoComplete="shipping address-line1"
								/>
							</Grid>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									name="shipping_zip"
									label="Código postal"
									fullWidth
									autoComplete="shipping postal-code"
								/>
							</Grid>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									name="shipping_phone"
									label="Teléfono"
									fullWidth
									autoComplete="phone"
								/>
							</Grid>
						</Grid>
						<Grid item xs={12} style={{marginTop: 70}}>
							<TextField
								name="order_comment"
								fullWidth
								label="Aquí puedes escribir alguna instrucción para la entrega de tu pedido. (Opcional). "
								autoComplete="shipping address-line1"
								multiline
								rowsMax={4}
							/>
						</Grid>
					</div>

					{/* cart total */}
					<div className="cart-totals-wrapper">
						<div className="cart-totals">
							<div className="cart-totals__items">
								<div className="item-name">Total parcial</div>
								<div className="item-value" style={{fontWeight: 900}}>{`$${order.subTotal}`}</div>
							</div>
							<div className="cart-totals__items" style={{flexDirection: "column"}}>
								<div className="shipping" style={{marginBottom: 0, fontSize: 20}}>Envío</div>
								<RadioGroup aria-label="gender" name="gender1" value={order.shippingType}>
									<FormControlLabel
										value={order.shippingType}
										control={<Radio/>}
										label={shippingLabel( order.shippingType )}
									/>
								</RadioGroup>

							</div>
							<div className="cart-totals__items" style={{fontSize: "20px", fontWeight: 600}}>
								<div className="item-name">Total</div>
								<div className="item-value">{`$${order.total}`}</div>
							</div>
						</div>
						<div className="checkout-payment" style={{marginTop: 30}}>
							<div className="cart-totals__items" style={{fontSize: "20px", fontWeight: 200}}>
								<div>Pago</div>
								<div style={{
									justifyContent: 'space-between',
									display: 'flex',
									width: '28%',
									marginRight: 20
								}}>
									<img src="images/mastercard.svg" alt="Master Card" style={{maxWidth: '30px'}}/>
									<img src="images/amex.svg" alt="American Express" style={{width: '30px'}}/>
									<img src="images/visa.svg" alt="Visa" style={{width: '30px'}}/>
								</div>
							</div>
						</div>

						<div style={{width: '100%', marginBottom: 10, marginTop: 10, border: "1px solid #e2e2e2"}}>
							<CardElement
								options={cardElementOpts}
								onChange={handleCardDetailsChange}
							/>
						</div>

						{checkoutError &&
						 <div style={{textAlign: 'center', color: 'red', fontSize: 15, fontWeight: 'bold'}}>
							 <span>{checkoutError}</span>
						 </div>}
						<div className="proceed-to-checkout">
							<BaseButton
								size="large"
								style={{margin: 0, marginBottom: 10, width: '100%'}}
								type="submit"
								disabled={isProcessing || !checkCompleteStripe}
							>
								{isProcessing ? 'Procesando...' : 'Realizar compra'}
							</BaseButton>
							<BaseButton
								size="large"	
								style={{margin: 0, width: '100%'}}
								component={RouterLink}
								to={'/cart'}
							>
								Carrito
							</BaseButton>
						</div>
					</div>
				</div>
			</form>
		</React.Fragment>
	);
}

const mapStateToProps = ( state ) => {
	return {
		items: state.cart.items,
		order: state.cart.order
	}
};

export default connect( mapStateToProps, {createOrder} )( withRouter(CheckoutForm) )