
export const ADD_TO_CART = 'ADD_TO_CART';
export const addToCart =  (product = {}) =>(dispatch) => (
    dispatch({
        type: ADD_TO_CART,
        payload: {
            product
        }
    })
);
export const DELETE_FROM_CART = 'DELETE_FROM_CART';
export const deleteFromCart = (product) => (
    {
        type: DELETE_FROM_CART,
        payload: product
    }
);

export const FETCH_PRODUCTS = 'FETCH_PRODUCTS';
export const fetchProducts = (products) => (
    {
        type: FETCH_PRODUCTS,
        payload: products,
        meta: {
            asPromise: true
        }
    }
);

export const APP_INIT = 'APP_INIT';
export const appInit = () => ({
    type: APP_INIT
});

export const CLEAR_STATE = 'CLEAR_STATE';
export const clearState = () => ({
    type: CLEAR_STATE
});

export const CHANGE_PRODUCT_PROP = 'CHANGE_PRODUCT_PROP';
export const changeProductProp = (prop, value, productId) => ({
    type: CHANGE_PRODUCT_PROP,
    payload: {
        prop,
        value,
        productId
    }
});

export const CHANGE_ORDER_PROP = 'CHANGE_ORDER_PROP';
export const changeOrderProp = (prop, value) => ({
    type: CHANGE_ORDER_PROP,
    payload: {
        prop,
        value
    }
});

export const CREATE_ORDER = 'CREATE_ORDER';
export const createOrder = (orderData) => ({
    type: CREATE_ORDER,
    payload: {
        request: {
            url: process.env.REACT_APP_BASE_URL_PROD + '/order',
            method: 'post',
            data: orderData
        }
    },
    meta: {
        asPromise: true
    }
});