import React from "react";
import "./CartItem.css"
import Product from "./Product";
import IconButton from "@material-ui/core/IconButton";
import ClearIcon from '@material-ui/icons/Clear';
import {connect} from "react-redux";
import {deleteFromCart, changeProductProp} from "../../store/cart/cartActions";
import TextField from "@material-ui/core/TextField";
import {MenuItem} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";

const CartItem = ( {deleteFromCart, changeProductProp, product} ) => {

	const framePrice = [
		500, 600, 950
	]

	const sizeMapPrice = [
		500, 600, 250
	]

	const onDeleteProduct = () => {
		// delete product from localStorage
		const items = JSON.parse(localStorage.getItem('starCart'));

		if (items.length <= 1) {
			localStorage.removeItem('starCart');
			localStorage.removeItem('starOrder');
		} else {
			localStorage.setItem('starCart', JSON.stringify(items.filter(item => item.id !== product.id)));
		}

		deleteFromCart( product )
	}

	const onChangeQuantity = ( event ) => {
		const framePriceValue = product.frameColor !== 0  ? framePrice[product.size] : 0;
		const newPrice = (sizeMapPrice[product.size] + framePriceValue) * event.target.value;

		changeProductProp('price', newPrice, product.id);
		changeProductProp('quantity', event.target.value, product.id);
	};
	const onChangeFrameColor = ( event ) => {
		const framePriceValue = event.target.value !== 0  ? framePrice[product.size] : 0;
		const newPrice = (sizeMapPrice[product.size] + framePriceValue) * product.quantity;

		changeProductProp('price', newPrice, product.id);
		changeProductProp('frameColor', event.target.value, product.id);
	};
	const onChangeMapSize = ( event ) => {
		const framePriceValue = product.frameColor !== 0  ? framePrice[event.target.value] : 0;
		const newPrice = (sizeMapPrice[event.target.value] + framePriceValue) * product.quantity;

		changeProductProp('price', newPrice, product.id);
		changeProductProp('size', event.target.value, product.id);
	};

	const frameColor = [
		{
			key: 0,
			label: 'Sin marco'
		},
		{
			key: 1,
			label: 'Madera'
		},
		{
			key: 2,
			label: 'Negra'
		},
		{
			key: 3,
			label: 'Blanca'
		}
	];

	const mapSize = [
		{
			key: 0,
			label: '30 X 40 cm'
		},
		{
			key: 1,
			label: '45 X 60 cm'
		},
		{
			key: 2,
			label: 'Digital'
		}
	];

	const useStyle = makeStyles(() => ({
		root: {
			background: 'transparent',
			verticalAlign: 'bottom !important',
			marginRight: 15,
			'& .MuiInputBase-root': {
				width: '20ch',
				background: 'transparent'
			},
			'& .MuiFormControl-root': {
				verticalAlign: 'bottom !important'
			}
		}
	}))

	const classes = useStyle();

	return (
		<div className="cart_item">
			<div className="cart-product">
				<IconButton onClick={onDeleteProduct} aria-label="delete" className="remove remove-product-button">
					<ClearIcon fontSize="small"/>
				</IconButton>
				<Product product={product}/>
			</div>

			{/* Quantity */}
			<TextField
				className={classes.root}
				id="product-quantity"
				label="Cantidad"
				type="number"
				onChange={onChangeQuantity}
				defaultValue={product.quantity}
				InputLabelProps={{
					shrink: true,
				}}
			/>

			{/* Frame type */}
			<TextField
				className={classes.root}
				id="product-frame-type"
				style={{background: 'transparent'}}
				select
				label="Color del marco"
				onChange={onChangeFrameColor}
				value={product.frameColor}
				variant="filled"
			>
				{frameColor.map(option => (
					<MenuItem key={option.key} value={option.key}>
						{option.label}
					</MenuItem>
				))}
			</TextField>

			{/* Map size */}
			<TextField
				className={classes.root}
				id="product-map-size"
				style={{background: 'transparent'}}
				select
				onChange={onChangeMapSize}
				label="Tamaño de mapa"
				value={product.size}
				variant="filled"
			>
				{mapSize.map(option => (
					<MenuItem key={option.key} value={option.key}>
						{option.label}
					</MenuItem>
				))}
			</TextField>
		</div>
	)
};

const mapStateToProps = ( state ) => {
	return {
		items: state.cart.items
	}
};

export default connect( mapStateToProps, {deleteFromCart, changeProductProp} )( CartItem )
