import React from 'react';
import './Product.css';
import appConstants from '../../configs/constants.config';

const Product = ( {product} ) => {

	const thumbnail = product.thumbnail || 'https://s3.us-east-2.amazonaws.com/tcdn.email.template.images/thumbnail.jpeg';

	return (
		<div className="product">
			<div className="product-thumbnail">
				<a href="#img1">
					<img alt="Product preview in cart"
					     src={thumbnail}
					     className="attachment-woocommerce_thumbnail size-woocommerce_thumbnail"
					/>
				</a>
			</div>
			<div className="product-info">
				<div className="product-name">

					{product.name.map(line => (
						<p style={{margin: 0}}>{line}</p>
					))}

					<p>{appConstants.SIZE[product.size]}</p>
				</div>
				<div className="product-description">
					<strong>Mensaje:</strong>
					<p>
						{product.message}
					</p>
				</div>
				<div className="product-details">
					<p>
						{product.date &&
							 <span>
								 <strong>Fecha: </strong>
								 {product.date}<br/>
							 </span>
						}
						{product.location &&
							 <span>
								 <strong>Ubicación: </strong>
								 {product.location}<br/>
							 </span>
						}
						{product.coordinate &&
							 <span>
								 <strong>Coordinar: </strong>
								 {product.coordinate}<br/>
							 </span>
						}
					</p>
				</div>
			</div>
			<div className="product-price">
				{`$${product.price}`}
			</div>
		</div>
	)
};

export default Product
